.App_main__3Lg8g {
  /* 134px is the height of the header + the footer */
  height: calc(100vh - 134px);

  /* 72px is the height of just the header */
  /* (the footer is not visible on such a small height) */
  min-height: 428px;
}

.App_container__eyuZg {
  height: 100%;
}

.Converter_row__2TQAN {
  /* 81 + 70 = 151 px where 81 is the height of the title and 70 is the height of the buttons */
  height: calc(100% - 151px) !important;
}

.Converter_textarea__2qcKs {
  /* 36px is the height of the subtitle title text */
  height: calc(100% - 36px) !important;

  font-family: Tinos, serif;
  font-size: 1.2rem !important;
}

#root {
  min-height: 500px;
}

